input {
    background-color: rgb(239 239 239 / 35%);
    font-size: 19px;
    padding: 20px;
    color: #fff;

    background: rgba(245, 214, 214, 0.25);
    box-shadow: 0 8px 32px 0 rgba(31, 135, 62, 0.37);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.18);
}

input::placeholder {
    color: #fff;
}